@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "open sans", sans-serif;
  font-size: 20px;
}

div.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  color: #222;
}

header {
  -webkit-flex-basis: 30vh;
          flex-basis: 30vh;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding: 30px 0;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

main aside {
  min-width: 240px;
}

main section.page {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

footer {
  background-color: #000;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

header {
  position: relative;
  overflow: hidden;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

nav.main {
  padding: 0px 30px 30px 0;
}

nav.main ul {
  list-style-type: none;
}

nav.main a {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  color: #000;
  padding: 10px;
  transition: all 0.2s;
}

nav.main a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}

nav.main a.active {
  border-left: 10px solid grey;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
  border-left-color: green;
}

nav.main li:nth-child(2) a.active {
  border-left-color: blue;
}

nav.main li:nth-child(3) a.active {
  border-left-color: orange;
}

nav.main li:nth-child(4) a.active {
  border-left-color: red;
}

.products h2 {
  margin-bottom: 20px;
}

.products ul {
  list-style: square;
  padding-left: 30px;
}

.products li {
  margin-bottom: 20px;
}

.products a {
  text-decoration: none;
  color: #333;
  transition: 0.1s;
}

.products a:hover {
  text-decoration: underline;
  color: blue;
}

.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.4;
  padding: 5px;
}

.contact button {
  display: block;
  margin: 0 20% 0 auto;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  padding: 10px 20px;
  background-color: #444;
  color: #fff;
  font-size: 20px;
  transition: 0.1s;
}

.contact button:hover {
  background-color: brown;
}

.footer span {
  color: red;
}

.footer h2 {
  font-size: 18px;
}

.footer p {
  font-size: 14px;
}

